import React, { useState,useEffect } from "react";
import { useLanguage } from '../components/LanguageContext';
import { faArrowRight , faLocationDot , faXmark , faChevronRight , faChevronLeft} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import payment from '../assets/payment.png'; 
import reviews from '../assets/reviews.png';
import { div } from "three/webgpu";
import { width } from "@fortawesome/free-solid-svg-icons/fa0";

const DonatePage = () => {
    const { language } = useLanguage('th');
    const [isMobile, setIsMobile] = useState(false);

    useEffect(() => {
        window.addEventListener('resize', handleResize);
        handleResize(); // Initial check
        return () => window.removeEventListener('resize', handleResize);
      }, []);
    
      const handleResize = () => {
        setIsMobile(window.innerWidth <= 1024);
      };
    

    const styles = {
        Page: {
            paddingTop:isMobile ? '100px' : '150px',
            width: '100%',          // Change width to 100% to use the full screen width
            boxSizing: 'border-box',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            margin: '0 auto',  // Center content horizontally
            gap:'16px',
        },
        content: {
            display: 'flex',
            flexDirection: isMobile ? 'column' : 'row',  // 
            width: isMobile ? '100%' : '95%',  // 
            justifyContent: isMobile ? 'center' : 'space-between', // 
            alignItems: isMobile ? 'center' : 'flex-start', // 
            padding: isMobile ? '0px 16px' : '0px',
            boxSizing: 'border-box'
        },
        title: {
            fontSize:isMobile ? '60px' :'72px',
            margin: 0,
            color: '#10443e',
            fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
            fontWeight: language === 'en' ? 'bold' : 'normal',
          },
          subtitle: {
            fontSize:isMobile ? '45px' :'72px',
            fontWeight: 'normal',
            color: '#ad9468',
            margin: 0,
            fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
            fontWeight: language === 'en' ? 'bold' : 'normal',
          },
          titleTH: {
            fontSize:isMobile ? '36px' :'72px',
            margin: 0,
            color: '#10443e',
            fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
            fontWeight: language === 'en' ? 'bold' : 'normal',
          },
          subtitleTH: {
            fontSize:isMobile ? '36px' :'72px',
            fontWeight: 'normal',
            color: '#ad9468',
            margin: 0,
            fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
            fontWeight: language === 'en' ? 'bold' : 'normal',
          },
        Details:{
            color:'#666666',
            fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
            justifyContent: 'center', 
            width: '100%'  // Make sure text section is not constrained
        },
        contentLeft:{
            display:'flex',
            textAlign:'left',
            width:isMobile ? '100%' :'55%',
             // Allow left content to take up 48% of the width
            flexDirection: 'column',
        },
        design:{
            borderTop: '1px solid #AD9468',
            gap:'24px',
            fontSize:isMobile ? '14px' :'24px',
        },
        designTH:{
            borderTop: '1px solid #AD9468',
            gap:'24px',
            fontSize:isMobile ? '20px' :'24px',
        },
        contentRight:{
            borderRadius:'30px',
            padding:'24px 16px',
            background:'#2e5853',
            display: 'flex',
            alignItems: 'center', 
            flexDirection: 'column',   
            textAlign: 'left',
            gap:'10px',
            width:isMobile ? '100%' :'38%',
            boxSizing:'border-box',
            marginBottom:isMobile ? '24px' :'0'
        },
        imgAbout:{
            width:isMobile ? '100%' :'80%',
        },
        paymentDetail: {
            display: "flex", //  flexbox
            flexDirection: "column", // 
            alignItems: "center", // 
            justifyContent: "center", // 
            textAlign: "center", // 
            margin: "20px", // 
          },
        imgRevies:{
            width:isMobile ? '90%' :'80%', 
        }
    }

    return(
        <div style={styles.Page}>
            <div style={styles.content}>
                <div style={styles.contentLeft}>
                    <div style={styles.Details}>
                        {language === 'en' ? (
                            <>
                                  <h1 style={styles.title}>
                                    Join Us
                                </h1>
                                <h2 style={styles.subtitle}>
                                    Supporting Our Work
                                </h2>
                                <div style={styles.design}>
                                    <p>Your support is a vital driving force in advancing our projects
                                        and services, enabling us to create a positive impact on 
                                        society and our community. Whether through donations or 
                                        sharing information, your contributions empower us to grow
                                        and provide even better services for the benefit of everyone.
                                    </p>
                                    <div style={styles.paymentDetail}>
                                        <p style={{color:'#2e5853' , fontSize:isMobile ? '18px' :'24px' , fontFamily: "'Kanit', sans-serif"}}>
                                            :    
                                        </p>
                                        <img src={payment} alt="payment" style={styles.imgAbout}/>
                                    </div>
                                </div>
                            </>
                        ) : (
                            <>
                                 <h1 style={styles.titleTH}>
                                    
                                </h1>
                                <h2 style={styles.subtitleTH}>
                                    
                                </h2>
                                <div style={styles.designTH}>
                                    <p>
                                         
                                         
                                        
                                    </p>
                                    <div style={styles.paymentDetail}>
                                        <p style={{color:'#2e5853' ,fontSize:isMobile ? '18px' :'24px' , fontFamily: "'Kanit', sans-serif"}}>
                                            :    
                                        </p>
                                        <img src={payment} alt="payment" style={styles.imgAbout}/>
                                    </div>
                                </div>
                            </>
                        )}
                    </div>
                </div>
                <div style={styles.contentRight}>
                
                {language === 'en' ? (
                    <p style={{
                        color:'#ffe095',
                        fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
                        fontSize:isMobile ? '30px' :'48px',
                        margin: 0,
                        gap:'4px',
                        borderBottom:'1px solid #ffe095',
                        textAlign: 'left',  // Ensure left alignment
                        display: 'flex',  // Use flexbox to ensure the icon and text are aligned in a row
                        alignItems: 'center',  // Center the icon vertically
                        justifyContent: 'center',  // Align the content to the left within the container
                    }}>
                        Thank you for your support
                    </p>
                ) : (
                    <p style={{
                        color:'#ffe095',
                        fontFamily:  language === 'en' ? "'Cormorant Garamond', serif" :  "'Kanit', sans-serif",
                        fontSize:isMobile ? '20px' :'40px',
                        margin: 0,
                        gap:'4px',
                        borderBottom:'1px solid #ffe095',
                        textAlign: 'left',  // Ensure left alignment
                        display: 'flex',  // Use flexbox to ensure the icon and text are aligned in a row
                        alignItems: 'center',  // Center the icon vertically
                        justifyContent: 'center',  // Align the content to the left within the container
                        
                    }}>
                       
                    </p>
                )}

                {language === 'en' ? (
                    <div style={{
                        boxSizing:'border-box',
                        display: "flex", //  flexbox
                        flexDirection: "column", // 
                        alignItems: "center", // 
                        justifyContent: "center", // 
                        textAlign: "center", // 
                        }}>
                        <p style={{
                           fontSize:isMobile ? '14px' :'24px',
                            color:'white',
                            textAlign:'left',  // Ensure text is aligned to the left
                            fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
                        }}>
                            We are ready to listen to your feedback, questions, or
                            any issues you encounter to continuously improve our
                            products. If you have questions about using our
                            services or experience any difficulties, we are here to
                            assist you with care and dedication.
                        </p>
                        <img src={reviews} alt="reviews" style={styles.imgRevies}/>
                    </div>
                ) : (
                    <div style={{
                        boxSizing:'border-box',
                        display: "flex", //  flexbox
                        flexDirection: "column", // 
                        alignItems: "center", // 
                        justifyContent: "center", // 
                        textAlign: "center", // 
                        }}>
                        <p style={{
                            fontSize:isMobile ? '14px' :'24px',
                            color:'white',
                            textAlign:'left',  // Ensure text is aligned to the left
                            fontFamily:  language === 'en' ?  "'Poppins', sans-serif" :  "'Kanit', sans-serif",
                        }}>
                              
                            
                            
                             
                        </p>
                        <img src={reviews} alt="reviews" style={styles.imgRevies}/>
                    </div>
                )}
            </div>

            </div>
        </div>
    );
};

export default DonatePage;
