import React, { useState } from "react";
import { motion } from "framer-motion";
import bucketImage from "../assets/seemsee.png";
import stickImage from "../assets/stickSeemsee.png";

const GameSeemsee = () => {
  const [stickNumber, setStickNumber] = useState(null); // 
  const [showBucket, setShowBucket] = useState(false); // /
  const [showStick, setShowStick] = useState(false); // /
  const [isButtonVisible, setIsButtonVisible] = useState(true); // /

  // 
  const drawFortuneStick = () => {
    const randomNum = Math.floor(Math.random() * 30) + 1; //  1-30
    setStickNumber(randomNum);
  };

  // 
  const startAnimation = () => {
    setIsButtonVisible(false); // 
    setShowBucket(false); // 
    setShowStick(false); // 
    setStickNumber(null); // 

    // 
    setTimeout(() => {
      setShowBucket(true);
      //  
      setTimeout(() => {
        setShowStick(true);
        drawFortuneStick(); // 
      }, 1500); // Delay 1.5 
    }, 500); // Delay 0.5 
  };

  return (
    <div style={{ textAlign: "center", marginTop: "20px" }}>
      {/*  */}
      {isButtonVisible && (
        <button
          onClick={startAnimation}
          style={{
            padding: "10px 20px",
            fontSize: "18px",
            cursor: "pointer",
            margin: "20px",
          }}
        >
          start
        </button>
      )}

      {/*  */}
      {showBucket && (
        <motion.div
          initial={{ opacity: 0 }} // 
          animate={{ opacity: 1 }} // 
          transition={{ duration: 1.5 }} // 
          style={{ position: "relative", display: "inline-block" }}
        >
          <img src={bucketImage} alt="" width="200px" />

          {/*  */}
          {showStick && (
            <motion.img
              src={stickImage}
              alt=""
              initial={{ y: 0 }} //  y = 0
              animate={{ y: -150 }} //  -150px
              transition={{
                duration: 0.8, // 
                repeat: 1, // 
                repeatType: "reverse", //  y = 0
                ease: "easeInOut", // 
              }}
              style={{
                position: "absolute",
                left: "50%",
                transform: "translateX(-50%)",
                bottom: "10px",
                width: "50px",
              }}
            />
          )}
        </motion.div>
      )}

      {/*  */}
      {stickNumber && (
        <p style={{ marginTop: "20px", fontSize: "24px", color: "red" }}>
           {stickNumber}
        </p>
      )}
    </div>
  );
};

export default GameSeemsee;
