import React from "react";

const MatterPortPage = () => {
  const styles = {
    container: {
      width: "100vw", // 
      height: "100vh", // 
      margin: 0,
      padding: 0,
      overflow: "hidden", //  scrollbars
    },
    iframe: {
      width: "100%", //  container
      height: "100%", //  container
      border: "none", //  iframe
    },
  };

  return (
    <div style={styles.container}>
      <iframe
        style={styles.iframe}
        src="https://my.matterport.com/show/?m=VBv2MTshKxC"
        allowFullScreen
        mozAllowFullScreen
        webkitAllowFullScreen
        allow="xr-spatial-tracking; clipboard-write"
      ></iframe>
    </div>
  );
};

export default MatterPortPage;
